<template>
  <div class="invoice">
    <el-form :model="addInvoiceForm" :rules="addInvoiceRules" ref="addInvoiceForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="" prop="code">
        <el-input v-model="addInvoiceForm.code" placeholder="3700114140"></el-input>
      </el-form-item>
      <el-form-item label="" prop="tax_cate">
        <el-select v-model="addInvoiceForm.tax_cate" placeholder="" style="width: 100%;">
          <el-option
                  v-for="item in taxCateOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="number">
        <el-input v-model="addInvoiceForm.number" placeholder="56898754"></el-input>
      </el-form-item>
      <el-form-item label="" prop="create_date">
        <el-date-picker
                style="width: 100%;"
                v-model="addInvoiceForm.create_date"
                type="date"
                value-format="yyyy-MM-dd"
                placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="" prop="saler_name">
        <el-input v-model="addInvoiceForm.saler_name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="saler_tax_no">
        <el-input v-model="addInvoiceForm.saler_tax_no" placeholder="91380913246514232T"></el-input>
      </el-form-item>
      <el-form-item label="" prop="money">
        <el-input v-model="addInvoiceForm.money" placeholder="10000.00"></el-input>
      </el-form-item>
      <el-form-item label="" prop="tax_money">
        <el-input v-model="addInvoiceForm.tax_money" placeholder="10000.00"></el-input>
      </el-form-item>
      <el-form-item label="" prop="state">
        <el-select v-model="addInvoiceForm.state" placeholder="" style="width: 100%;">
          <el-option
                  v-for="item in stateOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>



      <div class="text-center">
        <el-button type="primary" @click="saveInvoiceForm('addInvoiceForm')"></el-button>
      </div>
    </el-form>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    name: "AddInvoice",
    data(){
      return {
        addInvoiceForm: {
          code: '', // 
          tax_cate: '', // 
          number: '', // 
          create_date: '', // 
          saler_name: '', // 
          saler_tax_no: '', // 
          money: '', // 
          tax_money: '', // 
          state: '', // 
        },
        addInvoiceRules: {
          code: [{ required: true, message: '', trigger: 'blur' },],
          tax_cate: [{ required: true, message: '', trigger: 'blur' },],
          number: [{ required: true, message: '', trigger: 'blur' },],
          create_date: [{ required: true, message: '', trigger: 'blur' },],
          saler_name: [{ required: true, message: '', trigger: 'blur' },],
          saler_tax_no: [{ required: true, message: '', trigger: 'blur' },],
          money: [{ required: true, message: '', trigger: 'blur' },],
          tax_money: [{ required: true, message: '', trigger: 'blur' },],
          state: [{ required: true, message: '', trigger: 'blur' },],
        },
        //  select
        taxCateOptions: [
          {
            value: '',
            label: ''
          },
          {
            value: '',
            label: ''
          }
        ],
        //  select
        stateOptions: [
          {
            value: '1',
            label: ''
          },
          {
            value: '2',
            label: ''
          },
          {
            value: '3',
            label: ''
          },
          {
            value: '4',
            label: ''
          },
          {
            value: '5',
            label: ''
          },
        ],
        editCode: null, // null 
      }
    },
    watch: {
      editCode: function (val) {
        const trade_id = this.$store.state.preset.trade_id
        if (val == null) {
          this.addInvoiceForm.code = '', // 
              this.addInvoiceForm.tax_cate = '', // 
              this.addInvoiceForm.number = '', // 
              this.addInvoiceForm.create_date = '', // 
              this.addInvoiceForm.saler_name = '', // 
              this.addInvoiceForm.saler_tax_no = '', // 
              this.addInvoiceForm.money = '', // 
              this.addInvoiceForm.tax_money = '', // 
              this.addInvoiceForm.state = '' // 
        }
      }
    },
    methods:{
      saveInvoiceForm(formName){
        this.$refs[formName].validate((valid) => {
          if (valid) {
            var invoice = {...this.addInvoiceForm}
            invoice['cid'] = this.$store.state.preset.trade_id

            axios.post(host + 'invoice', invoice).then(res => {
              if(res.data.error_code == 0){
                this.$message.success('')
                this.handleReset(formName)
                layer.close(layer.index); //
              }else{
                this.$message.error('')
              }
            })
          } else {
            this.$message.error('')
            return false;
          }
        });
      },
      // 
      handleReset(name) {
        this.$refs[name].resetFields();
      },
    }
  }
</script>

<style>
  .el-popper{
    z-index: 999999999 !important;
  }
</style>
<style scoped>
  .invoice{
    padding: 15px;
  }
</style>



