<template>
  <div class="material">
    <div class="mb-5 company-top">
      <div class="float-left title-tag"></div>
      <el-button type="success" class="float-right mr-10 success" size="mini" @click="toPreset('add')"></el-button>
    </div>

    <div v-if="companyName != ''">
      <!--+-->
      <div class="company-info">
        <div class="row justify-content-between">
          <div class="float-left company-title">
            <strong class="company-name">{{companyName}}</strong>
          </div>
          <div class="float-right text-center company-operating">
            <el-button type="text" class="mr-10" @click="addBusiness()"></el-button>
            <el-button type="text" class="mr-10" @click="toPreset('addInvoice')"></el-button>
            <el-button type="text" v-if="practicalAnswer == 1" class="mr-10" @click="toPracticalAnswer"></el-button>
            <el-button type="text" v-else class="mr-10" @click="toPracticalAnswer"></el-button>
            <el-button type="text" class="mr-10" @click="toPreset('edit')"></el-button>
            <el-button type="text" class="mr-10" @click="delCompany('company')"></el-button>
          </div>
        </div>
      </div>
      <div class="table-responsive">
        <el-table :data="businessData" border style="width: 100%">
          <el-table-column prop="content" label=""></el-table-column>
          <el-table-column fixed="right" label="" width="100">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="editResponsive(scope.$index, businessData)"></el-button>
              <el-button type="text" size="small" @click="delResponsive(scope.$index, businessData)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!---->
      <div class="mt-15 text-center">
        <el-pagination
                @current-change="handleCurrentChange"
                layout="prev, pager, next"
                :page-size='companyList.pageSize'
                :current-page="page"
                :total="companyList.total">
        </el-pagination>
      </div>
    </div>
    <div v-else class="airInfo"></div>
    <!---->
    <div v-show="showCompanyadd">
      <Popups ref="popups">
        <div slot="title"></div>
        <div slot="close">
          <i class="el-icon-close" @click="showCompanyaddoff"></i>
        </div>
        <div slot="new-content" class="company-table">
          <el-tabs v-model="tabsName" type="card" @tab-click="handleClick">
            <el-tab-pane v-if="showTabsType != 1" label="" name="companyInfo">
              <!---->
              <el-form :model="companyForm" label-position="right" :rules="companyRules" ref="companyForm"
                       label-width="100px" class="demo-ruleForm">
                <el-form-item label="" prop="company" style="width: 450px">
                  <el-input v-model="companyForm.company" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="taxnumber" style="width: 450px">
                  <el-input v-model="companyForm.taxnumber" placeholder="91370305755666003U"></el-input>
                </el-form-item>
                <el-form-item label="" prop="certifypwd" style="width: 450px">
                  <el-input v-model="companyForm.certifypwd" placeholder="000000"></el-input>
                </el-form-item>
                <el-form-item label="" prop="sub_date" style="width: 450px">
                  <el-input v-model="companyForm.sub_date" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="level">
                  <el-select v-model="companyForm.level" placeholder="" style="width: 340px">
                    <el-option
                            v-for="item in companyOptions"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                    </el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="" prop="check_date">
                  <el-date-picker
                          style="width: 340px"
                          v-model="companyForm.check_date"
                          type="date"
                          format="yyyy-MM-dd"
                          value-format="yyyy-MM-dd"
                          placeholder="">
                  </el-date-picker>
                </el-form-item>
                <el-form-item label="" prop="confirm_date">
                  <el-date-picker
                          style="width: 340px"
                          v-model="companyForm.confirm_date"
                          type="date"
                          format="yyyy-MM-dd"
                          value-format="yyyy-MM-dd"
                          placeholder="">
                  </el-date-picker>
                </el-form-item>
                <el-form-item label="" prop="tax_date">
                  <el-date-picker
                          style="width: 340px"
                          v-model="companyForm.tax_date"
                          type="daterange"
                          format="yyyy-MM-dd"
                          value-format="yyyy-MM-dd"
                          range-separator="-"
                          start-placeholder=""
                          end-placeholder="">
                  </el-date-picker>
                </el-form-item>
                <el-form-item label="" prop="check_start">
                  <el-date-picker
                          style="width: 340px"
                          v-model="companyForm.check_start"
                          type="daterange"
                          format="yyyy-MM-dd"
                          value-format="yyyy-MM-dd"
                          range-separator="-"
                          start-placeholder=""
                          end-placeholder="">
                  </el-date-picker>
                </el-form-item>
                <div class="text-center">
                  <el-button type="success" @click="saveCompanyInfo('companyForm')"></el-button>
                </div>
              </el-form>
            </el-tab-pane>
            <el-tab-pane v-if="showTabsType == 1" label="" name="invoiceInfo">
              <!---->
              <div>
                <el-button class="f-r" style="margin-bottom: 10px;" type="success" @click="addInvoice"></el-button>
              </div>
              <el-table
                      height="445px"
                      :data="invoiceData"
                      border
                      style="width: 100%">
                <el-table-column prop="code" label="" width="100"></el-table-column>
                <el-table-column prop="tax_cate" label="" width="120"></el-table-column>
                <el-table-column prop="number" label=""></el-table-column>
                <el-table-column prop="create_time" label="" width="100"></el-table-column>
                <el-table-column prop="saler_name" label="" width="200"></el-table-column>
                <el-table-column prop="saler_tax_no" label="" width="200"></el-table-column>
                <el-table-column prop="money" label=""></el-table-column>
                <el-table-column prop="tax_money" label=""></el-table-column>
                <el-table-column prop="state" label="">
                  <template slot-scope="scope">{{ scope.row.state == 1? '': scope.row.state == 2? '':scope.row.state
                    == 3? '':scope.row.state == 4? '':scope.row.state == 5? '': ''}}
                  </template>
                </el-table-column>
                <el-table-column
                        fixed="right"
                        label=""
                        width="120">
                  <template slot-scope="scope">
                    <el-button
                            @click.native.prevent="invoiceHandleEdit(scope.$index, invoiceData)"
                            type="text"
                            size="small">
                      
                    </el-button>
                    <el-button
                            @click.native.prevent="invoiceHandleDelete(scope.$index, invoiceData)"
                            type="text"
                            size="small">
                      
                    </el-button>
                  </template>
                </el-table-column>
              </el-table>
            </el-tab-pane>
          </el-tabs>
        </div>
      </Popups>
    </div>
    <!---->
    <div v-show="showInvoiceAdd">
      <div class="invoiceAdd">
        <InvoiceAdd ref="invoiceAdd"></InvoiceAdd>
      </div>
    </div>
    <!---->
    <div v-show="showPreseteAdd">
      <div class="preseteAdd">
        <Preseteadd ref="preseteadd"></Preseteadd>
      </div>
    </div>
  </div>
</template>

<script>
  import Popups from './pop-ups-model'
  import InvoiceAdd from './preset/addInvoice' // 
  import Preseteadd from './preset/presetadd' // 
  import axios from 'axios'

  export default {
    name: "Material",
    components: {
      Popups,
      InvoiceAdd,
      Preseteadd
    },
    mounted() {
      this.getCompanyList()
    },
    data() {
      return {
        companyName: '',
        showInvoiceAdd: 0,
        showTabsType: 0,
        showPreseteAdd: 0,
        practicalAnswer: 0, //  0  1
        // 
        businessData: [],
        showCompanyadd: 0, // 
        tabsName: 'companyInfo', // tabs
        // 
        companyForm: {
          company: '', // 
          taxnumber: '', // 
          certifypwd: '', // 
          level: '', // 
          sub_date: '', // 
          tax_date: '', // 
          check_start: '', // 
          check_date: '', // 
          confirm_date: '', // 
        },
        companyRules: {
          company: [{required: true, message: '', trigger: 'blur'}],
          taxnumber: [{required: true, message: '', trigger: 'blur'}],
          certifypwd: [{required: true, message: '', trigger: 'blur'}],
          level: [{required: true, message: '', trigger: 'blur'}],
          sub_date: [{required: true, message: '', trigger: 'blur'}],
          tax_date: [{required: true, message: '', trigger: 'blur'}],
          check_start: [{required: true, message: '', trigger: 'blur'}],
          check_date: [{required: true, message: '', trigger: 'blur'}],
          confirm_date: [{required: true, message: '', trigger: 'blur'}],
        },
        // ->
        companyOptions: [
          {
            value: '1',
            label: 'A'
          },
          {
            value: '2',
            label: 'B'
          },
          {
            value: '3',
            label: 'C'
          },
          {
            value: '4',
            label: 'M'
          }
        ],
        // 
        invoiceData: [],
        // 
        companyList: {
          total: 0, // 
          pageSize: 1 //
        },
        // 
        page: 1,
      }
    },
    methods: {
      // 
      getCompanyList() {
        this.$store.commit('delState') // 
        this.companyName = ''
        axios.post(host + 'company/list?page=' + this.page + '&page_size=1').then(res => {
          if (res.data.error_code == 0) {
            if (res.data.data.data[0]) {
              var arr = res.data.data.data[0]
              this.$store.commit('saveCompany', arr)

              // 
              this.companyList.total = res.data.data.total // 
              this.$store.commit('saveTradeid', arr.id) // id
              this.companyName = arr.company // 
              // 
              this.tableDataInfo()
              // 
              this.invoiceDataInfo()
              // 
              axios.get(host + 'answer/' + arr.id).then(attr => {
                if (attr.data.error_code == 0) {
                  if (attr.data.data[0] != null) {
                    if (Object.keys(attr.data.data[0]).length != 0) {
                      this.practicalAnswer = 1
                    } else {
                      this.practicalAnswer = 0
                    }
                  } else {
                    this.practicalAnswer = 0
                  }
                }
              })
            }
          } else {
            this.$message.error('')
          }
        })
      },
      // 
      toPreset(res) {
        if (res == 'add') { // 
          this.showCompanyadd = 1
          this.showTabsType = 0
          this.handleReset('companyForm') // 
          var arr = {}
          arr['name'] = 'companyInfo'
          this.handleClick(arr) // tabs
          this.tabsName = 'companyInfo'


        } else if (res == 'edit') { // 
          var trade_id = this.$store.state.preset.trade_id
          if (trade_id) {
            this.showCompanyadd = 1
            this.showTabsType = 0
            var arr = {}
            arr['name'] = 'companyInfo'
            this.handleClick(arr) // tabs
            this.tabsName = 'companyInfo'
            var comInfo = {...this.$store.state.preset.companyInfo.company[trade_id]}
            this.companyForm = comInfo
          } else {
            this.$message.error('')
          }
        } else if (res == 'addInvoice') {
          var trade_id = this.$store.state.preset.trade_id
          if (trade_id) {
            this.showCompanyadd = 1
            this.showTabsType = 1
            var arr = {}
            arr['name'] = 'invoiceInfo'
            this.handleClick(arr) // tabs
            this.tabsName = 'invoiceInfo'
          } else {
            this.$message.error('')
          }
        }
      },
      // 
      saveCompanyInfo(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            var attr = {...this.companyForm}
            axios.post(host + 'company/add', attr).then(res => {
              if (res.data.error_code == 0) {
                // 
                this.$store.commit('saveCompany', attr)
                this.handleReset('companyForm')
                this.showCompanyadd = 0
                this.getCompanyList()
                this.$message.success('')
              } else {
                this.$message.error('')
              }
            })
          } else {
            this.$message.error('')
            return false;
          }
        });
      },
      // 
      showCompanyaddoff() {
        this.showCompanyadd = 0
      },
      // 
      delCompany(res) {
        var trade_id = this.$store.state.preset.trade_id
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          axios.delete(host + 'company/del', {id: trade_id}).then(res => {
            if (res.data.error_code == 0) {
              this.getCompanyList()
              this.$message.success('')
            } else {
              this.$message.error('')
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });

      },
      // 
      addBusiness() {
        const trade_id = this.$store.state.preset.trade_id;
        if (trade_id) {
          this.showPreseteAdd = 1
          layui.use('layer', () => {
            var layer = layui.layer;
            setTimeout(() => {
              var index = layer.open({
                type: 1,
                title: '',
                shadeClose: false,
                shade: false,
                area: ['460px', '480px'],
                content: J('.preseteAdd'),
                end: () => {
                  this.showPreseteAdd = 0
                  // 
                  this.tableDataInfo();
                },
                success: () => {
                  this.$refs.preseteadd.presetCode = null;    // 
                }
              });
            }, 5)
          });
        } else {
          this.$message.error('')
        }
      },
      // 
      tableDataInfo() {
        const trade_id = this.$store.state.preset.trade_id
        // 
        // this.businessData = this.$store.state.preset.companyInfo.business[trade_id]

        axios.get(host + 'business/' + trade_id).then(res => {
          if (res.data.error_code) {
            this.$Message.error(res.data.msg)
          } else {
            // 
            this.$store.commit('delState', {name: 'business'})
            for (var i = 0; i < (res.data.data).length; i++) {
              this.$store.commit('saveBusiness', {name: trade_id, data: res.data.data[i]})
            }
            this.businessData = res.data.data
          }
        })
      },
      // 
      editResponsive(code, row) {
        this.showPreseteAdd = 1
        layui.use('layer', () => {
          var layer = layui.layer;
          setTimeout(() => {
            var index = layer.open({
              type: 1,
              title: '',
              shadeClose: false,
              shade: false,
              area: ['460px', '480px'],
              content: J('.preseteAdd'),
              end: () => {
                this.showPreseteAdd = 0
                // 
                this.tableDataInfo();
              },
              success: () => {
                var arr = {...row[code]}
                this.$refs.preseteadd.preset = arr;    // 
                this.$refs.preseteadd.presetCode = code;    // 
              }
            });
          }, 5)
        });
      },
      // 
      delResponsive(index, row) {
        axios.delete(host + 'business/' + row[index].id).then(res => {
          if (res.data.error_code == 0) {
            this.$message.success('')
            // 
            this.tableDataInfo();
          } else {
            this.$message.error('')
          }
        })
      },
      // 
      toPracticalAnswer() {
        var trade_id = this.$store.state.preset.trade_id;
        if (trade_id) {
          // 
          // console.log('ID')
          // id  /teacher =>   / => 
          window.open('/ren_zheng/#/' + trade_id, "_blank");
        } else {
          this.$message.error('')
        }
      },
      // 
      handleCurrentChange(val) {
        console.log(val)
        this.page = val
        this.getCompanyList()
      },
      // tabs
      handleClick(tab) {
        if (tab.name === 'invoiceInfo') { // 
          this.$refs.popups.modelWidth.width = '900px';
        } else if (tab.name === 'companyInfo') { // 
          this.$refs.popups.modelWidth.width = '490px';
        }
      },
      // 
      // 
      addInvoice() {
        layui.use('layer', () => {
          var layer = layui.layer;
          this.showInvoiceAdd = 1; // 
          setTimeout(() => {
            var index = layer.open({
              type: 1,
              title: '',
              shadeClose: false,
              shade: false,
              area: ['490px', '555px'],
              content: J('.invoiceAdd'),
              end: () => {
                this.showInvoiceAdd = false
                this.invoiceDataInfo()
              },
              success: () => {
                this.$refs.invoiceAdd.editCode = null;    // 
              }
            });
          }, 1)
        });
      },
      // 
      invoiceDataInfo() {
        const trade_id = this.$store.state.preset.trade_id
        // 
        axios.get(host + 'invoice/' + trade_id).then(res => {
          if (res.data.error_code == 0) {
            this.invoiceData = res.data.data
          } else {
            this.$message.error('')
          }
        })
      },
      // 
      invoiceHandleEdit(index, row) {
        layui.use('layer', () => {
          var layer = layui.layer;
          this.showInvoiceAdd = 1; // 
          setTimeout(() => {
            layer.open({
              type: 1,
              title: '',
              shadeClose: false,
              shade: false,
              area: ['490px', '555px'],
              content: J('.invoiceAdd'),
              end: () => {
                this.showInvoiceAdd = false
                this.invoiceDataInfo()
              },
              success: () => {
                var arr = {...row[index]}
                this.$refs.invoiceAdd.addInvoiceForm = arr;    // 
                this.$refs.invoiceAdd.editCode = index;    // 
              }
            });
          }, 1)
        });
      },
      // 
      invoiceHandleDelete(index, row) {
        axios.delete(host + 'invoice/' + row[index].id).then(res => {
          if (res.data.error_code == 0) {
            this.$message.success('')
            this.invoiceDataInfo()
          } else {
            this.$message.error('')
          }
        })
      },
      // 
      handleReset(name) {
        this.$nextTick(() => {
          this.$refs[name].resetFields()
        })
      },
    }
  }
</script>

<style>
  .material .el-table__header th {
    background-color: #f8f8f9 !important;
  }

  .material .table-responsive .el-table__header th:nth-child(2) .cell {
    text-align: center;
  }

  .material .company-table .el-table__header th .cell {
    text-align: center;
  }

  .material .el-tab-pane label {
    width: 110px !important;
  }

  .material .el-form-item__content {
    margin-left: 110px !important;
  }

  .material .el-tabs__content {
    /*height: 375px;*/
    overflow: auto;
  }
</style>
<style scoped>
  .material {
    width: 100%;
    height: 100%;
    padding: 20px;
    overflow: auto;
  }

  .material .airInfo {
    margin-top: 50px;
    text-align: center;
    font-size: 14px;
    color: #909399;
  }

  .material .title-tag {
    font-size: 14px;
    font-weight: 600;
  }

  .material .success {
    background-color: #93C343;
    border-color: #93C343;
  }

  .material .success:hover {
    background-color: #B4D57C;
    border-color: #B4D57C;
  }

  .material .successhalf:hover {
    background-color: #F4F9EC;
    border-color: #C3DE97;
    color: #A7CE66;
  }

  .company-info {
    height: 42px;
    line-height: 30px;
    border: 1px solid #dee2e6;
    border-bottom: 0px;
  }

  .company-info .justify-content-between {
    padding-left: 10px;
    height: 100%;
  }

  .company-info .company-title {
    line-height: 40px;
  }

  .company-info .company-operating {
    width: 320px;
    line-height: 40px;
    float: right;
  }

  .company-name {
    font-size: 13px;
    color: #515a6e;
  }

  .company-top {
    height: 35px;
  }
</style>



