<template>
  <div class="frame">
    <div>
      <!---->
      <div class="header bg-green">
        <div class="f-l logo">
          <img src="../assets/images/logo-cer.png">
        </div>
        <div class="f-r user">
          <a target="">{{com.company}}</a> &nbsp;|&nbsp; <a @click="toAbort" style="cursor: pointer;"></a>
        </div>
        <div class="clear"></div>
        <el-menu
                style="height: 48px;padding-left: 50px;"
                :default-active="activeIndex2"
                class="el-menu-demo"
                mode="horizontal"
                background-color="#1F84C6"
                text-color="#fff"
                active-text-color="#fff">
          <el-menu-item index="1">
            <a :href="[urlType == 0 ? 'javascript:;' : '#/home/indexcertificate']"><div></div></a>
          </el-menu-item>
          <el-submenu index="2">
            <template slot="title"></template>
            <el-menu-item index="2-1">
              <a class="color-black" :href="[urlType == 0 ? 'javascript:;' : '#/home/check']"><div></div></a>
            </el-menu-item>
            <el-menu-item index="2-2">
              <a class="color-black" :href="[urlType == 0 ? 'javascript:;' : '#/home/upload']"><div></div></a>
            </el-menu-item>
            <el-menu-item index="2-3">
              <a class="color-black" :href="[urlType == 0 ? 'javascript:;' : '#/home/deduction']"><div></div></a>
            </el-menu-item>
            <el-menu-item index="2-4"></el-menu-item>
            <el-menu-item index="2-5"></el-menu-item>
            <el-menu-item index="2-6">
              <a class="color-black" :href="[urlType == 0 ? 'javascript:;' : '#/home/count']"><div></div></a>
            </el-menu-item>
          </el-submenu>
          <el-menu-item index="3"></el-menu-item>
          <el-menu-item index="4"></el-menu-item>
          <el-submenu index="5">
            <template slot="title"></template>
            <el-menu-item index="5-1"></el-menu-item>
            <el-menu-item index="5-2"></el-menu-item>
            <el-menu-item index="5-3"></el-menu-item>
          </el-submenu>
          <el-menu-item index="6"></el-menu-item>
          <el-menu-item index="7"></el-menu-item>
          <el-menu-item index="8"></el-menu-item>
          <el-menu-item index="9">
            <!---->
            <a :href="[urlType == 0 ? 'javascript:;' : '#/home/info']"><div></div></a>
          </el-menu-item>
          <el-menu-item @click="saveAnswer"></el-menu-item>
        </el-menu>
      </div>
    </div>
    <!--home infosetPersonnelInfourl-->
    <router-view></router-view>
    <!---->
    <div class="footer bg-green">
      
    </div>
  </div>
</template>

<script>
  import common from '../mixins/common'

  export default {
    name: "Home",
    mixins: [common],
    data() {
      return {
        hasCompany: 0, //   
        urlType: 1,
        // newdata
        activeIndex: '1',
        activeIndex2: '1',
        com: ''
      }
    },
    mounted() {
      // 
      this.synchronizeComInfo()
    },
    methods:{
      saveAnswer() {
        this.saveBusiness()
      },
      // 
      toAbort() {
        this.$router.push('/')
      }
    }
  }
</script>

<style>
  .frame .header .el-menu--horizontal>.el-submenu .el-submenu__title,
  .frame .header .el-menu--horizontal>.el-menu-item{
    height: 47px;
    line-height: 47px;
  }
</style>
<style scoped>
  a div{
    width: 100%;
    height: 100%;
  }
  .color-black{
    color: #ffffff;
  }
  .frame {
    width: 100%;
    height: 100%;
    overflow: auto;
  }
  .footer{
    background: #474747;
    width: 100%;
    padding: 20px 0;
    margin: 0 auto;
    line-height: 30px;
    text-align: center;
    color: #aaa;
  }
  .do-next{
    background: url("../assets/images/do_next.png");
  }
  .header {
    min-width: 1280px;
  }
</style>



