<template>
  <div class="frame">
    <el-container style="overflow:auto;">
      <el-header style="height: 120px;">
        <!---->
        <div class="bg-f login-head">
          <div class="width-1200 relative">
            <div class="f-l">
              <img src="../assets/images/login-logo.gif">
            </div>
            <div class="f-r ribbon">
              <a href="javascript:void(0);" title="" class="lczy notice1">
                <span></span>
              </a>
              <a href="javascript:void(0);" title="" class="jchj notice1">
                <span></span>
              </a>
              <a href="javascript:void(0);" title="" class="sjgg notice1">
                <span></span>
              </a>
              <a href="javascript:void(0);" title="" class="cjwt notice1">
                <span></span>
              </a>
              <a v-if="hasCompany" href="javascript:void(0);" title="" class="czsc change-trade">
                <span></span>
              </a>
              <a v-else href="javascript:void(0);" title="" class="czsc notice1">
                <span></span>
              </a>
            </div>
            <div class="clear"></div>
          </div>
        </div>
      </el-header>
      <el-container style="min-width: 1250px;">
        <el-main>
          <!---->
          <div class="login-cont">
            <div class="width-1200 relative">
              <div class="login_box">
                <div class="mbbox">
                  <ul>
                    <li id="mb2" class="font-18 hover"></li>
                  </ul>
                </div>
                <div class="conbox">
                  <div id="con_mb_2">
                    <div class="pd-vertical-10">
                      <div class="login_input">
                        <span></span>
                        <el-input prefix-icon="iconfont new_iconupan" v-model="certifyPwd" style="width: 74%;" placeholder="/"></el-input>
                      </div>
                    </div>
                    <div v-if="platPwdType">
                      <div class="pd-vertical-10">
                        <div class="login_input">
                          <span></span>
                          <el-input prefix-icon="iconfont new_iconupan" v-model="platPwd" style="width: 74%;" placeholder=""></el-input>
                        </div>
                      </div>
                      <div class="f-r mg-right-5">
                        <a href="#" class="for_key for_key_alert"></a>
                      </div>
                      <div class="clear"></div>
                    </div>
                    <div class="pd-vertical-10">
                      <!--<input class="sub_1" @click="toLogin" value="">-->
                      <el-button type="primary" class="sub_1" @click="toLogin"></el-button>
                    </div>
                    <!--<div class="login_word">-->
                      <!--<span><a>12345678</a></span>-->
                    <!--</div>-->
                  </div>
                </div>
              </div>
            </div>
          </div>
        </el-main>
        <el-footer>
          <div class="width-1200">
            <!---->
            
          </div>
        </el-footer>
      </el-container>
    </el-container>
    <!---->
    <AnswerBoard v-if="boardStatus" class="answerBoard">
      <div v-if="boardType" slot="content">
        {{businessInfo.content}}
      </div>
      <div v-else slot="material">
        <!--<p><Tag color="blue"></Tag></p>{{businessInfo.material}}-->
      </div>
      <div slot="buttons">
        <el-button class="mr-5 mt-10 mr-10 float-right" type="primary" size="mini" @click="businessSwitch('next')"></el-button>
        <el-button class="mr-5 mt-10 mr-10 float-right" type="primary" size="mini" @click="businessSwitch('previous')"></el-button>
        <el-button v-if="companyStatus != 0" class="mr-5 mt-10 float-right" type="primary" size="mini" @click="saveBusiness">
          
        </el-button>
        <el-button v-if="companyStatus == 0 && sumbitType" class="mr-5 mt-10 float-right" type="primary" size="mini"
                @click="saveBusiness">
        </el-button>
        <!--<Button class="mr-5" v-if="boardType" @click="materialTagSwitch" ></Button>-->
        <!--<Button class="mr-5" v-else type="primary" ghost @click="materialTagSwitch" ></Button>-->
      </div>
    </AnswerBoard>
    <div v-else class="do-next" @click="closeBoard"></div>
  </div>
</template>

<script>
  import AnswerBoard from './answer-board'
  import common from '../mixins/common'
  import axios from 'axios'
  export default {
    name: "Index",
    mixins: [common],
    components:{
      AnswerBoard
    },
    data() {
      return {
        businessCode: 0, // 
        boardStatus: 0, // 
        businessInfo: {}, // 
        boardType: 1, // /
        companyStatus: 0, // 
        sumbitType: 0,


        hasCompany: 0, //   
        platPwdType: 0, // 
        platPwd: '', // 
        certifyPwd: '' // 
      }
    },
    mounted: function () {
      // state
      this.$store.commit('delState')
      var url_trade_id = this.$router.history.current.query.tid
      // 
      if(url_trade_id){
        axios.post(host + '/company/list',{id: url_trade_id}).then(res => {
          var com = res.data.data.data[0]

          // 
          var preset = {}
          preset['trade_id'] = url_trade_id
          preset['businessCode'] = 0
          preset['platPwdType'] = 0 // 
          preset['operatorStatus'] = 1 //  0: 1:
          preset['companyInfo'] = {}
          if(!preset['companyInfo']['company']){
            preset['companyInfo']['company'] = {}
          }
          if(!preset['companyInfo']['preset']){
            preset['companyInfo']['preset'] = {}
          }
          if(!preset['companyInfo']['business']){
            preset['companyInfo']['business'] = {}
          }

          preset['companyInfo']['company'][url_trade_id] = com
          preset['companyInfo']['preset'][url_trade_id] = com.preset
          preset['companyInfo']['business'][url_trade_id] = com.business

          this.$store.commit('clonePreset', preset) // 

          // 
          if((res.data.data.data[0].invoice).length != 0){
            this.$store.commit('cloneInvoiceList', res.data.data.data[0].invoice)
          }else{
            this.$store.commit('cloneInvoiceList', com.preset)
          }

          setTimeout(() => {
            // 
            this.closeBoard()
            // 
            this.companyStatus = 1
            // 
            this.setBusinessInfo();
            // 
            this.businessCode = this.$store.state.preset.businessCode
            // 
            var pubData = this.$store.state.publicData
            if(pubData['plat'] != undefined){
              this.platPwdType = pubData['plat'].platEnable
            }

          },50)
        })
        axios.get(host + '/company/answer/'+ url_trade_id).then(res => {
          if(res.data.error_code == 0){
            if(res.data.data[0] != null){
              if(res.data.data[0]['Info'] == undefined){
                res.data.data[0]['Info'] = {
                  Email: "",
                  address: "",
                  name: "",
                  tel: "",
                  zipCode: "",
                }
              }else if(res.data.data[0]['plat'] == undefined){
                res.data.data[0]['plat'] = {
                  disabled: "0",
                  editPlat: "0",
                  platAnswer: "",
                  platEnable: "",
                  platQuestion: "",
                  pwdone: "",
                  pwdtwo: "",
                }
              }
              this.$store.commit('clonePublicData', res.data.data[0]) // 
            }
          }else{
            this.$message.error('')
          }
        })
      }else{
        this.$message.error('')
      }
    },
    methods:{
      toLogin(){
        // 
        var trade_id = this.$store.state.preset.trade_id
        var certifyPwd = this.$store.state.preset.companyInfo.company[trade_id].certifypwd
        if(this.$store.state.publicData.plat != undefined){
          var platPwd = this.$store.state.publicData.plat.pwdone
        }
        if(this.certifyPwd){
          // 
          if(this.certifyPwd == certifyPwd){
            // 
            if(this.platPwdType){
              if(this.platPwd == platPwd){
                this.$router.push('/home/info')
              }else{
                this.$message({
                  message: '',
                  type: 'warning'
                });
              }
            }else{
              this.$router.push('/home/info')
            }
          }else{
            this.$message({
              message: '',
              type: 'warning'
            });
          }
        }else{
          this.$message.error('');
        }
      },
    }
  }
</script>

<style scoped>
  .frame {
    width: 100%;
    height: 100%;
    padding-top: 140px;
  }
  .el-header {
    color: #333;
    text-align: center;
    line-height: 30px;
  }
  .el-footer {
    padding: 30px;
    color: #333;
    text-align: center;
    /*line-height: 15px;*/
  }

  .el-aside {
    background-color: #D3DCE6;
    color: #333;
    text-align: center;
    line-height: 200px;
  }

  .el-main {
    color: #333;
    text-align: center;
    padding: 0px;
  }

  body > .el-container {
    margin-bottom: 40px;
  }

  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 260px;
  }

  .el-container:nth-child(7) .el-aside {
    line-height: 320px;
  }
  .login-cont {
    background: url("../assets/images/login_bg.gif") center bottom no-repeat #a2f3f7;
  }
  .do-next{
    background: url("../assets/images/do_next.png");
  }
</style>



