export default {
  methods: {
    // 
    saveBusiness(){
      const trade_id = this.$store.state.preset.trade_id
      if(this.$store.state.publicData){
        const data = this.$store.state.publicData
        const answer = JSON.stringify(data)
        let formdata = new FormData()
        formdata.append('data', answer)
        formdata.append('company_id', this.$store.state.preset.companyInfo.company[trade_id].id)
        // 
        this.post('/Api/RenZheng/saveAnswer', formdata, res => {
          if(res.status) {
            this.$message.success('')
          } else {
            this.$message.error(res.info)
          }
        })
      }else{
        this.$message.error('')
      }
    },
    post (url, data, fn) {
      let obj = new XMLHttpRequest()
      obj.open('POST', url, true)
      obj.onreadystatechange = function () {
        if (+obj.readyState === 4 && (+obj.status === 200 || +obj.status === 304 || obj.status === 0)) {
          console.log(obj)
          fn.call(this, JSON.parse(obj.responseText))
        }
      }
      obj.send(data)
    },
    // 
    synchronizeComInfo(){
      var trade_id = this.$store.state.preset.trade_id
      this.com = this.$store.state.preset.companyInfo.company[trade_id]
    },
    /**
     * truefalse
     * @param dateString 
     * @param startDateString 
     * @param endDateString 
     * @returns {Number}
     */
    isDateBetween(dateString, startDateString, endDateString) {
      if (dateString == '') {
        console.log("dateString");
        return;
      }
      if (startDateString == '') {
        console.log("startDateString");
        return;
      }
      if (endDateString == '') {
        console.log("endDateString");
        return;
      }
      var flag = false;
      var startFlag = false
      var endFlag = false

      if(Date.parse(dateString) >= Date.parse(startDateString)){
        startFlag = true
      }
      if(Date.parse(dateString) <= Date.parse(endDateString)){
        endFlag = true
      }
      if (startFlag && endFlag) {
        flag = true;
      }
      return flag;
    },
    // 
    // attr 
    // type  1: 2:
    setInvoiceInfo(attr){
      var res = {
        zyfpNum: 0, // 
        zyfpMoney: 0, // 
        zyfpTax_money: 0, // 

        jdcNum: 0, // 
        jdcMoney: 0, // 
        jdcTax_money: 0, // 

        numTotal: 0, // 
        moneyTotal: 0, // 
        tax_moneyTotal: 0, // 
      }
      for(var i=0; i<attr.length; i++){
        if(attr[i].tax_cate == ''){
          res.zyfpNum++ // 
          res.zyfpMoney = res.zyfpMoney*1 + attr[i].money*1 // 
          res.zyfpTax_money = res.zyfpTax_money*1 + attr[i].tax_money*1 // 
        }else if(attr[i].tax_cate == ''){
          res.jdcNum++ // 
          res.jdcMoney = res.jdcMoney*1 + attr[i].money*1 // 
          res.jdcTax_money = res.jdcTax_money*1 + attr[i].tax_money*1 // 
        }
      }

      res.numTotal = res.zyfpNum*1 + res.jdcNum*1
      res.moneyTotal = res.zyfpMoney*1 + res.jdcMoney*1
      res.tax_moneyTotal = res.zyfpTax_money*1 + res.jdcTax_money*1

      return res
    },
    // 
    conversionTime(time) {
      let date = new Date(parseInt(time) * 1000);
      let y = date.getFullYear();
      let m = date.getMonth() + 1;
      m = m < 10 ? ('0' + m) : m;
      let d = date.getDate();
      d = d < 10 ? ('0' + d) : d;
      // let h = date.getHours();
      // h = h < 10 ? ('0' + h) : h;
      // let minute = date.getMinutes();
      // let second = date.getSeconds();
      // minute = minute < 10 ? ('0' + minute) : minute; second = second < 10 ? ('0' + second) : second;
      // console.log( y + '-' + m + '-' + d + ' ' + '' + h + ':' + minute + ':' + second)
      // let dates = y + '-' + m + '-' + d + ' ' + '' + h + ':' + minute + ':' + second;
      let dates = y + '-' + m + '-' + d;
      return dates;
    },
    // 
    changeTwoDecimal_f(x) {
      var f_x = parseFloat(x);
      if (isNaN(f_x)) {
        alert('function:changeTwoDecimal->parameter error');
        return false;
      }
      var f_x = Math.round(x * 100) / 100;
      var s_x = f_x.toString();
      var pos_decimal = s_x.indexOf('.');
      if (pos_decimal < 0) {
        pos_decimal = s_x.length;
        s_x += '.';
      }
      while (s_x.length <= pos_decimal + 2) {
        s_x += '0';
      }
      return s_x;
    }
  }
}



